export const angularReservedKeywords = [
    'CLASS',
    'NODENAME',
    'NODETYPE',
    'COMPONENT',
    'SUBSCRIPTION',
    'RESPONSE',
    'OBSERVABLE',
    'INJECTABLE',
    'HTTP',
    'ROUTER',
];
export const isReservedAngularKeyword = (keyword) => {
    return angularReservedKeywords.includes(keyword.toUpperCase());
};
